<script lang="ts">
import { defineComponent, ref, computed, onMounted } from 'vue'
import { achievementService, type Achievement, type AchievementCategory } from '@/services/achievementService'

export default defineComponent({
  name: 'AchievementsView',
  setup() {
    const newUnlockedAchievements = ref<Achievement[]>([])
    
    // Ëé∑ÂèñÊàêÂ∞±Êï∞ÊçÆ
    const achievements = computed(() => achievementService.getAllAchievements())
    const achievementCategories = computed(() => achievementService.getAchievementCategories())
    const achievementStats = computed(() => achievementService.getAchievementStats())
    
    // Âà∑Êñ∞ÊàêÂ∞±
    const refreshAchievements = () => {
      const newUnlocked = achievementService.checkAchievements()
      if (newUnlocked.length > 0) {
        newUnlockedAchievements.value = newUnlocked
      }
    }
    
    // ÈáçÁΩÆÊàêÂ∞±
    const resetAchievements = () => {
      if (confirm('Á°ÆÂÆöË¶ÅÈáçÁΩÆÊâÄÊúâÊàêÂ∞±ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊí§ÈîÄ„ÄÇ')) {
        achievementService.resetAchievements()
        newUnlockedAchievements.value = []
      }
    }
    
    // ÂÖ≥Èó≠ÈÄöÁü•
    const dismissNotifications = () => {
      newUnlockedAchievements.value = []
    }
    
    // Ê†ºÂºèÂåñÊó•Êúü
    const formatDate = (timestamp: number) => {
      return new Date(timestamp).toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      })
    }
    
    onMounted(() => {
      // È°µÈù¢Âä†ËΩΩÊó∂Ê£ÄÊü•ÊàêÂ∞±
      refreshAchievements()
    })
    
    return {
      achievements,
      achievementCategories,
      achievementStats,
      newUnlockedAchievements,
      refreshAchievements,
      resetAchievements,
      dismissNotifications,
      formatDate
    }
  }
})
</script>

